import React from "react";
import makeStyles from "@mui/styles/makeStyles";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import { useNavigate, useParams } from "react-router-dom";
import { connect } from "react-redux";
import doctorDefault from "../../assets/doctors.png";
import TopNav from "../../components/TopNav";
import Container from "@mui/material/Container";
import Dialog from "@mui/material/Dialog";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import List from "@mui/material/List";
import ListItemText from "@mui/material/ListItemText";
import { BASE_URL_IMG } from "../../helpers/common/config";
import Close from "@mui/icons-material/Close";
import { getJadwalDokter, setAppointment } from "../../redux/actions";

const useStyles = makeStyles(() => ({
  img: {
    width: "100%",
    // height: "auto",
    height: "500px",
    objectFit: "contain",
    position: "absolute",
    zIndex: "-1",
    top: 0,
    left: 0,
    right: 0,
  },
  footer: {
    position: "absolute",
    bottom: 0,
    left: 0,
    right: 0,
    height: "60%",
    padding: "32px",
    borderRadius: "32px 32px 0 0 !important",
  },
  container: {
    marginTop: "32px",
  },
}));
const BoxSum = ({ title, subtitle }) => {
  return (
    <Box border={1} padding={1} borderRadius={3}>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <Typography variant="body1">{title}</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="subtitle1">{subtitle}</Typography>
        </Grid>
      </Grid>
    </Box>
  );
};
const DetailDoctor = (props) => {
  const classes = useStyles();
  let { doctorId } = useParams();
  const navigate = useNavigate();
  const {
    stateDetailDokter,
    actGetJadwalDokter,
    stateJadwalDokter,
    stateBahasa,
    actSetAppointment,
    stateUser,
  } = props;
  const [openJadwal, setOpenJadwal] = React.useState(false);

  const handleJadwalDokter = () => {
    actGetJadwalDokter({
      doctor_id: doctorId,
    });
    setOpenJadwal(true);
  };

  const handleDlgJadwal = () => {
    setOpenJadwal(!openJadwal);
  };

  const handleAppointment = () => {
    if (stateDetailDokter) {
      actSetAppointment({
        layanan: {
          id_poli: stateDetailDokter.ref_layanan_id,
          ref_layanan_nama: stateDetailDokter.ref_layanan_nama,
          disabled: true,
        },
        dokter: {
          peg_id: stateDetailDokter.peg_id,
          nama_dokter: stateDetailDokter.nama_dokter,
          disabled: true,
        },
        total: 0,
      });
      navigate("/make-appointment");
    }
  };

  return (
    <>
      <Dialog open={openJadwal} fullWidth onClose={handleDlgJadwal}>
        <Paper
          sx={{
            padding: "8px",
          }}
        >
          <Grid container>
            <Grid item container justifyContent="end" xs={12}>
              <IconButton onClick={handleDlgJadwal}>
                <Close />
              </IconButton>
            </Grid>
            <Grid
              item
              xs={12}
              justifyContent="center"
              container
              direction="column"
              alignItems="center"
            >
              {stateJadwalDokter.length > 0 ? (
                <>
                  <Typography variant="subtitle2">
                    {stateJadwalDokter[0].ref_layanan_nama}
                  </Typography>
                  <Typography variant="body1">
                    {stateJadwalDokter[0].periode}
                  </Typography>
                </>
              ) : (
                <>
                  <List>
                    <ListItem disablePadding>
                      <ListItemButton>
                        <ListItemText
                          primary={
                            stateBahasa === "indonesia"
                              ? "Jadwal Kosong"
                              : "No Schedule"
                          }
                        />
                      </ListItemButton>
                    </ListItem>
                  </List>
                </>
              )}
            </Grid>
            <Grid item xs={12}>
              {stateJadwalDokter.length > 0 ? (
                stateJadwalDokter[0].jadwal.map((item, index) => {
                  return (
                    <div key={index + "jadwal"}>
                      <List>
                        <ListItem disablePadding>
                          <ListItemButton>
                            <ListItemText primary={item.hari_praktek} />
                            <ListItemText
                              primary={
                                item.jam_praktek_mulai.substring(0, 5) +
                                " - " +
                                item.jam_praktek_akhir.substring(0, 5)
                              }
                            />
                          </ListItemButton>
                        </ListItem>
                      </List>
                      <Divider />
                    </div>
                  );
                })
              ) : (
                <>
                  <List>
                    <ListItem disablePadding>
                      <ListItemButton>
                        <ListItemText
                          primary={
                            stateBahasa === "indonesia"
                              ? "Jadwal Kosong"
                              : "No Schedule"
                          }
                        />
                      </ListItemButton>
                    </ListItem>
                  </List>
                </>
              )}
            </Grid>
          </Grid>
        </Paper>
      </Dialog>
      <Container className={classes.container}>
        <TopNav
          type="back"
          title={
            stateBahasa === "indonesia" ? "Detail Dokter" : "Doctor Detail"
          }
          click={() => {
            navigate(-1);
          }}
        />
      </Container>
      <img
        className={classes.img}
        src={
          stateDetailDokter.filename
            ? BASE_URL_IMG + "/" + stateDetailDokter.filename
            : doctorDefault
        }
        alt=""
      />
      <Paper className={classes.footer}>
        <Grid container spacing={2}>
          <Grid item container spacing={2} xs={12}>
            <Grid item xs={6}>
              <Grid item xs={12}>
                <Typography variant="subtitle1">
                  {stateDetailDokter.nama_dokter}
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography variant="body1">
                  {stateDetailDokter.ref_layanan_nama}
                </Typography>
              </Grid>
            </Grid>
            <Grid item xs={6}>
              <Grid item xs={12}>
                <Typography align="right" variant="body1">
                  {stateBahasa === "indonesia" ? "Mulai Dari" : "Start From"}
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography align="right" variant="subtitle1">
                  -
                </Typography>
              </Grid>
            </Grid>
          </Grid>
          <Grid item container spacing={2}>
            <Grid item xs>
              <BoxSum
                title={stateBahasa === "indonesia" ? "Pasien" : "Patient"}
                subtitle={"-"}
              />
            </Grid>
            <Grid item xs>
              <BoxSum
                title={
                  stateBahasa === "indonesia" ? "Pengalaman" : "Experience"
                }
                subtitle={"-"}
              />
            </Grid>
            <Grid item xs>
              <BoxSum title={"Review"} subtitle={"-"} />
            </Grid>
          </Grid>
          <Grid item container spacing={2}>
            <Grid item xs={12}>
              <Button
                variant="contained"
                onClick={handleAppointment}
                disabled={!stateDetailDokter || !stateUser}
              >
                {stateBahasa === "indonesia"
                  ? "Buat Jadwal"
                  : "Make Appointment"}
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Button variant="outlined" onClick={handleJadwalDokter}>
                {stateBahasa === "indonesia" ? "Jadwal Praktek" : "Schedule"}
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    stateDetailDokter: state.master.detailDoctor || "",
    stateJadwalDokter: state.master.jadwalDokter,
    stateBahasa: state.app.bahasa,
    stateUser: state.auth.token,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actGetJadwalDokter: (body) => dispatch(getJadwalDokter(body)),
    actSetAppointment: (body) => dispatch(setAppointment(body)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(DetailDoctor);
